import React from 'react';

// Lucide React icons
import { 
  Brain, 
  MessageSquare, 
  FileText, 
  Bell, 
  Calendar,
  Zap,
  TrendingUp,
  Users,
  Shield,
  Mail,
  Download,
  Eye,
  Clock,
  CheckCircle2,
  AlertTriangle,
  ArrowUpRight,
  Bot,
  Lightbulb,
  BarChart3,
  DollarSign,
  Receipt,
  Target,
  CalendarDays,
  Sparkles,
  Search,
  Settings,
  HelpCircle
} from 'lucide-react';

// Mock data for AI Business Assistant platform
const dashboardData = {
  businessMetrics: {
    revenue: { current: 125000, target: 150000, progress: 83, change: 15 },
    invoices: { sent: 47, paid: 35, pending: 12, collectionRate: 74 },
    compliance: { completed: 8, pending: 3, overdue: 0, score: 82 },
    aiUsage: { queries: 89, documents: 23, replies: 45, efficiency: 68 }
  },
  aiAssistant: {
    unreadMessages: 3,
    recentQueries: [
      { id: 1, question: "VAT filing deadline for Q4 2024?", timestamp: "2 hours ago", category: "compliance" },
      { id: 2, question: "How to renew trade license in Dubai?", timestamp: "1 day ago", category: "legal" },
      { id: 3, question: "Best practices for payroll in UAE?", timestamp: "3 days ago", category: "hr" }
    ],
    recommendations: [
      { id: 1, type: "optimization", title: "Reduce Operational Costs", description: "AI identified 15% savings opportunity in vendor contracts", impact: "high" },
      { id: 2, type: "compliance", title: "VAT Regulation Update", description: "New guidelines effective next month - update your processes", impact: "critical" }
    ]
  },
  complianceAlerts: [
    { id: 1, type: "vat", title: "VAT Filing Due", deadline: "2024-03-20", daysLeft: 15, priority: "high", status: "upcoming" },
    { id: 2, type: "license", title: "Trade License Renewal", deadline: "2024-04-15", daysLeft: 41, priority: "medium", status: "upcoming" },
    { id: 3, type: "payroll", title: "Monthly Payroll", deadline: "2024-02-05", daysLeft: 5, priority: "high", status: "urgent" }
  ],
  quickActions: [
    { 
      id: 1, 
      icon: FileText, 
      label: "Generate Invoice", 
      description: "Create professional invoices with VAT calculation",
      color: "primary",
      premium: false
    },
    { 
      id: 2, 
      icon: Brain, 
      label: "AI Assistant", 
      description: "Get instant business advice and compliance help",
      color: "primary",
      premium: false
    },
    { 
      id: 3, 
      icon: Shield, 
      label: "Compliance Check", 
      description: "Verify UAE regulations and deadlines",
      color: "secondary",
      premium: true
    },
    { 
      id: 4, 
      icon: Mail, 
      label: "Auto-Reply Setup", 
      description: "Configure AI-powered WhatsApp replies",
      color: "secondary",
      premium: true
    },
    { 
      id: 5, 
      icon: Download, 
      label: "Export Reports", 
      description: "Download PDF and Excel business reports",
      color: "secondary",
      premium: false
    },
    { 
      id: 6, 
      icon: Users, 
      label: "Team Management", 
      description: "Manage staff access and permissions",
      color: "secondary",
      premium: true
    }
  ],
  aiInsights: [
    { 
      id: 1, 
      type: "opportunity", 
      title: "Growth Opportunity Detected", 
      message: "Based on your revenue trends, consider expanding to Abu Dhabi free zone. Projected 25% growth potential.",
      confidence: 85,
      actions: ["View Analysis", "Contact Advisor"]
    },
    { 
      id: 2, 
      type: "warning", 
      title: "Compliance Regulation Change", 
      message: "New VAT regulation changes effective March 1st. Update your billing system to avoid penalties.",
      confidence: 92,
      actions: ["Review Changes", "Update Templates"]
    },
    { 
      id: 3, 
      type: "efficiency", 
      title: "Process Optimization Available", 
      message: "Automate 70% of customer queries using our AI reply templates. Save 10+ hours weekly.",
      confidence: 78,
      actions: ["Setup Automation", "View Templates"]
    }
  ],
  recentActivity: [
    { id: 1, action: "invoice_created", target: "Invoice #INV-0248", time: "10:30 AM", amount: "AED 4,500", status: "sent" },
    { id: 2, action: "ai_query", target: "VAT compliance question", time: "09:15 AM", category: "compliance", status: "resolved" },
    { id: 3, action: "reminder_set", target: "License renewal", time: "Yesterday", days: 45, status: "active" },
    { id: 4, action: "document_generated", target: "Employment contract", time: "Yesterday", type: "legal", status: "completed" }
  ]
};

const Dashboard = () => {
  // Color scheme based functions
  const getPriorityColor = (priority: string) => {
    switch (priority) {
      case 'high': return 'text-red-600 bg-red-50 border-red-200';
      case 'medium': return 'text-amber-600 bg-amber-50 border-amber-200';
      case 'low': return 'text-[#344767] bg-[#F4F7FA] border-[#E1E8F5]';
      default: return 'text-[#344767] bg-[#F4F7FA] border-[#E1E8F5]';
    }
  };

  const getInsightIcon = (type: string) => {
    switch (type) {
      case 'opportunity': return <Lightbulb className="w-5 h-5 text-green-600" />;
      case 'warning': return <AlertTriangle className="w-5 h-5 text-[#F6A821]" />;
      case 'efficiency': return <Zap className="w-5 h-5 text-[#2E69A4]" />;
      default: return <Lightbulb className="w-5 h-5 text-[#344767]" />;
    }
  };

  const getActionColor = (colorType: string) => {
    if (colorType === 'primary') {
      return 'bg-[#1B2A49] text-white border-[#1B2A49] hover:bg-[#152238] hover:border-[#152238]';
    }
    return 'bg-white text-[#2E69A4] border-[#2E69A4] hover:bg-[#2E69A4] hover:text-white';
  };

  const getStatusColor = (status: string) => {
    switch (status) {
      case 'paid': return 'text-green-600 bg-green-50';
      case 'pending': return 'text-[#F6A821] bg-amber-50';
      case 'sent': return 'text-[#2E69A4] bg-blue-50';
      case 'resolved': return 'text-green-600 bg-green-50';
      default: return 'text-[#344767] bg-[#F4F7FA]';
    }
  };

  const getProgressColor = (percentage: number) => {
    if (percentage >= 80) return 'text-green-600 bg-green-50';
    if (percentage >= 60) return 'text-[#F6A821] bg-amber-50';
    return 'text-red-600 bg-red-50';
  };

  return (
    <div className="min-h-screen bg-[#F4F7FA] p-6">
      {/* Header Section */}
      <div className="mb-8">
        <div className="flex items-center justify-between">
          <div>
            <h1 className="text-2xl font-bold text-[#1B2A49] mb-2 flex items-center">
              <Brain className="w-8 h-8 mr-3 text-[#2E69A4]" />
              AI Business Assistant
            </h1>
            <p className="text-[#344767]">Smart compliance, invoicing, and business management powered by AI</p>
          </div>
          <div className="flex items-center space-x-3">
            <div className="bg-white px-4 py-2 rounded-lg border border-[#E1E8F5] shadow-sm">
              <span className="text-sm text-[#344767]">Business Health:</span>
              <span className="ml-2 font-bold text-[#1B2A49]">{dashboardData.businessMetrics.compliance.score}/100</span>
              <TrendingUp className="w-4 h-4 text-green-500 inline ml-1" />
            </div>
          </div>
        </div>
      </div>

      {/* Comprehensive Stats Cards */}
      <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-6 mb-8">
        {/* Revenue Card */}
        <div className="bg-white rounded-xl shadow-sm p-6 border border-[#E1E8F5]">
          <div className="flex items-center justify-between mb-4">
            <div className="p-2 bg-blue-50 rounded-lg">
              <DollarSign className="w-6 h-6 text-[#2E69A4]" />
            </div>
            <span className={`px-2 py-1 rounded-full text-xs font-medium ${getProgressColor(dashboardData.businessMetrics.revenue.progress)}`}>
              {dashboardData.businessMetrics.revenue.progress}% of target
            </span>
          </div>
          <h3 className="text-lg font-semibold text-[#1B2A49] mb-1">
            AED {dashboardData.businessMetrics.revenue.current.toLocaleString()}
          </h3>
          <p className="text-sm text-[#344767] mb-2">Quarterly Revenue</p>
          <div className="flex items-center justify-between">
            <span className="text-xs text-[#344767]">Target: AED {dashboardData.businessMetrics.revenue.target.toLocaleString()}</span>
            <span className="text-green-600 text-sm font-medium flex items-center">
              +{dashboardData.businessMetrics.revenue.change}% <TrendingUp className="w-3 h-3 ml-1" />
            </span>
          </div>
        </div>

        {/* Invoices Card */}
        <div className="bg-white rounded-xl shadow-sm p-6 border border-[#E1E8F5]">
          <div className="flex items-center justify-between mb-4">
            <div className="p-2 bg-green-50 rounded-lg">
              <Receipt className="w-6 h-6 text-[#2E69A4]" />
            </div>
            <span className={`px-2 py-1 rounded-full text-xs font-medium ${getProgressColor(dashboardData.businessMetrics.invoices.collectionRate)}`}>
              {dashboardData.businessMetrics.invoices.collectionRate}% collected
            </span>
          </div>
          <h3 className="text-lg font-semibold text-[#1B2A49] mb-1">
            {dashboardData.businessMetrics.invoices.sent} Invoices
          </h3>
          <p className="text-sm text-[#344767] mb-2">This Month</p>
          <div className="flex items-center justify-between">
            <span className="text-xs text-[#344767]">
              {dashboardData.businessMetrics.invoices.paid} paid • {dashboardData.businessMetrics.invoices.pending} pending
            </span>
          </div>
        </div>

        {/* Compliance Card */}
        <div className="bg-white rounded-xl shadow-sm p-6 border border-[#E1E8F5]">
          <div className="flex items-center justify-between mb-4">
            <div className="p-2 bg-amber-50 rounded-lg">
              <Shield className="w-6 h-6 text-[#2E69A4]" />
            </div>
            <span className="px-2 py-1 bg-green-50 text-green-600 rounded-full text-xs font-medium">
              On Track
            </span>
          </div>
          <h3 className="text-lg font-semibold text-[#1B2A49] mb-1">
            {dashboardData.businessMetrics.compliance.completed}/{dashboardData.businessMetrics.compliance.completed + dashboardData.businessMetrics.compliance.pending} Tasks
          </h3>
          <p className="text-sm text-[#344767] mb-2">Compliance Status</p>
          <div className="flex items-center justify-between">
            <span className="text-xs text-[#344767]">
              {dashboardData.businessMetrics.compliance.pending} pending • {dashboardData.businessMetrics.compliance.overdue} overdue
            </span>
          </div>
        </div>

        {/* AI Usage Card */}
        <div className="bg-white rounded-xl shadow-sm p-6 border border-[#E1E8F5]">
          <div className="flex items-center justify-between mb-4">
            <div className="p-2 bg-purple-50 rounded-lg">
              <Brain className="w-6 h-6 text-[#2E69A4]" />
            </div>
            <span className={`px-2 py-1 rounded-full text-xs font-medium ${getProgressColor(dashboardData.businessMetrics.aiUsage.efficiency)}`}>
              {dashboardData.businessMetrics.aiUsage.efficiency}% efficiency
            </span>
          </div>
          <h3 className="text-lg font-semibold text-[#1B2A49] mb-1">
            {dashboardData.businessMetrics.aiUsage.queries} AI Queries
          </h3>
          <p className="text-sm text-[#344767] mb-2">Productivity Boost</p>
          <div className="flex items-center justify-between">
            <span className="text-xs text-[#344767]">
              {dashboardData.businessMetrics.aiUsage.documents} docs • {dashboardData.businessMetrics.aiUsage.replies} replies
            </span>
          </div>
        </div>
      </div>

      {/* AI Assistant & Quick Actions */}
      <div className="grid grid-cols-1 lg:grid-cols-3 gap-6 mb-6">
        {/* AI Assistant Panel */}
        <div className="bg-white rounded-xl shadow-sm p-6 border border-[#E1E8F5] lg:col-span-2">
          <div className="flex items-center justify-between mb-6">
            <h2 className="text-lg font-semibold text-[#1B2A49] flex items-center">
              <Bot className="w-5 h-5 mr-2 text-[#2E69A4]" />
              AI Assistant Recommendations
            </h2>
            <div className="flex items-center space-x-2">
              <span className="px-2 py-1 bg-green-50 text-green-600 rounded-full text-xs font-medium">
                {dashboardData.aiAssistant.unreadMessages} new insights
              </span>
            </div>
          </div>

          {/* AI Recommendations */}
          <div className="space-y-4 mb-6">
            {dashboardData.aiAssistant.recommendations.map((rec) => (
              <div key={rec.id} className="p-4 rounded-lg border border-[#E1E8F5] hover:border-[#2E69A4] transition-colors">
                <div className="flex items-start space-x-3">
                  <Sparkles className="w-5 h-5 text-[#F6A821] mt-0.5" />
                  <div className="flex-1">
                    <div className="flex items-center justify-between mb-1">
                      <h3 className="font-medium text-[#1B2A49]">{rec.title}</h3>
                      <span className={`px-2 py-1 rounded-full text-xs font-medium ${
                        rec.impact === 'critical' ? 'bg-red-50 text-red-600' : 'bg-[#2E69A4] text-white'
                      }`}>
                        {rec.impact} impact
                      </span>
                    </div>
                    <p className="text-sm text-[#344767] mb-3">{rec.description}</p>
                    <div className="flex space-x-2">
                      <button className="text-sm bg-[#1B2A49] text-white px-4 py-2 rounded-lg hover:bg-[#152238] transition-colors font-medium">
                        View Details
                      </button>
                      <button className="text-sm border border-[#2E69A4] text-[#2E69A4] px-4 py-2 rounded-lg hover:bg-[#2E69A4] hover:text-white transition-colors font-medium">
                        Learn More
                      </button>
                    </div>
                  </div>
                </div>
              </div>
            ))}
          </div>

          {/* Recent AI Queries */}
          <div>
            <h3 className="font-medium text-[#1B2A49] mb-3">Recent AI Interactions</h3>
            <div className="space-y-3">
              {dashboardData.aiAssistant.recentQueries.map((query) => (
                <div key={query.id} className="flex items-center justify-between p-3 bg-[#F4F7FA] rounded-lg">
                  <div className="flex items-center space-x-3">
                    <MessageSquare className="w-4 h-4 text-[#2E69A4]" />
                    <div>
                      <p className="text-sm font-medium text-[#1B2A49]">{query.question}</p>
                      <p className="text-xs text-[#344767]">{query.timestamp} • {query.category}</p>
                    </div>
                  </div>
                  <CheckCircle2 className="w-4 h-4 text-green-500" />
                </div>
              ))}
            </div>
          </div>
        </div>

        {/* Professional Quick Actions */}
        <div className="bg-white rounded-xl shadow-sm p-6 border border-[#E1E8F5]">
          <div className="flex items-center justify-between mb-6">
            <h2 className="text-lg font-semibold text-[#1B2A49]">Quick Actions</h2>
            <Zap className="w-5 h-5 text-[#F6A821]" />
          </div>
          
          <div className="grid grid-cols-1 gap-3">
            {dashboardData.quickActions.map((action) => (
              <button 
                key={action.id}
                className={`w-full p-4 rounded-lg border-2 transition-all duration-200 hover:shadow-md flex items-center space-x-3 ${getActionColor(action.color)}`}
              >
                <div className={`p-2 rounded-lg ${
                  action.color === 'primary' ? 'bg-white/20' : 'bg-[#F4F7FA]'
                }`}>
                  <action.icon className="w-5 h-5" />
                </div>
                <div className="flex-1 text-left">
                  <div className="flex items-center space-x-2">
                    <span className="font-semibold text-sm">{action.label}</span>
                    {action.premium && (
                      <span className="px-2 py-1 bg-[#F6A821] text-white rounded text-xs font-medium">PRO</span>
                    )}
                  </div>
                  <span className="block text-xs opacity-90 mt-1">{action.description}</span>
                </div>
                <ArrowUpRight className={`w-4 h-4 ${
                  action.color === 'primary' ? 'text-white/80' : 'text-[#2E69A4]'
                }`} />
              </button>
            ))}
          </div>

          {/* AI Productivity Stats */}
          <div className="mt-6 p-4 bg-gradient-to-r from-[#F4F7FA] to-blue-50 rounded-lg border border-[#E1E8F5]">
            <div className="flex items-center space-x-2 mb-2">
              <BarChart3 className="w-4 h-4 text-[#2E69A4]" />
              <span className="font-medium text-sm text-[#1B2A49]">AI Productivity</span>
            </div>
            <p className="text-xs text-[#344767] mb-2">
              Saved <strong>12.5 hours</strong> this week through automation
            </p>
            <div className="w-full bg-[#E1E8F5] rounded-full h-2">
              <div 
                className="bg-[#2E69A4] h-2 rounded-full transition-all duration-500" 
                style={{ width: '68%' }}
              ></div>
            </div>
          </div>
        </div>
      </div>

      {/* Compliance & AI Insights */}
      <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">
        {/* Compliance Alerts */}
        <div className="bg-white rounded-xl shadow-sm p-6 border border-[#E1E8F5]">
          <div className="flex items-center justify-between mb-6">
            <h2 className="text-lg font-semibold text-[#1B2A49] flex items-center">
              <Shield className="w-5 h-5 mr-2 text-[#2E69A4]" />
              Compliance Alerts
            </h2>
            <button className="text-[#2E69A4] text-sm font-medium flex items-center hover:text-[#1B2A49] transition-colors">
              View Calendar <ArrowUpRight className="w-4 h-4 ml-1" />
            </button>
          </div>

          <div className="space-y-3">
            {dashboardData.complianceAlerts.map((alert) => (
              <div key={alert.id} className={`p-4 rounded-lg border ${getPriorityColor(alert.priority)}`}>
                <div className="flex items-center justify-between mb-2">
                  <div className="flex items-center space-x-2">
                    <CalendarDays className="w-4 h-4" />
                    <h3 className="font-medium text-[#1B2A49]">{alert.title}</h3>
                  </div>
                  <span className={`px-2 py-1 rounded-full text-xs font-medium ${getPriorityColor(alert.priority)}`}>
                    {alert.priority} priority
                  </span>
                </div>
                <p className="text-sm text-[#344767] mb-3">Due: {alert.deadline} • {alert.daysLeft} days left</p>
                <div className="flex space-x-2">
                  <button className="text-sm bg-[#1B2A49] text-white px-4 py-2 rounded-lg hover:bg-[#152238] transition-colors font-medium">
                    Set Reminder
                  </button>
                  <button className="text-sm border border-[#2E69A4] text-[#2E69A4] px-4 py-2 rounded-lg hover:bg-[#2E69A4] hover:text-white transition-colors font-medium">
                    View Details
                  </button>
                </div>
              </div>
            ))}
          </div>
        </div>

        {/* AI Insights */}
        <div className="bg-white rounded-xl shadow-sm p-6 border border-[#E1E8F5]">
          <div className="flex items-center justify-between mb-6">
            <h2 className="text-lg font-semibold text-[#1B2A49] flex items-center">
              <Brain className="w-5 h-5 mr-2 text-[#2E69A4]" />
              AI Business Insights
            </h2>
            <span className="px-2 py-1 bg-[#F6A821] text-white rounded-full text-xs font-medium">
              Live Analysis
            </span>
          </div>

          <div className="space-y-4">
            {dashboardData.aiInsights.map((insight) => (
              <div key={insight.id} className="p-4 rounded-lg border border-[#E1E8F5] hover:border-[#2E69A4] transition-colors">
                <div className="flex items-start space-x-3">
                  {getInsightIcon(insight.type)}
                  <div className="flex-1">
                    <div className="flex items-center justify-between mb-1">
                      <h3 className="font-medium text-[#1B2A49]">{insight.title}</h3>
                      <span className="text-xs bg-[#2E69A4] text-white px-2 py-1 rounded-full">
                        {insight.confidence}% confidence
                      </span>
                    </div>
                    <p className="text-sm text-[#344767] mb-3">{insight.message}</p>
                    <div className="flex space-x-2">
                      {insight.actions.map((action, index) => (
                        <button key={index} className="text-sm border border-[#2E69A4] text-[#2E69A4] px-3 py-1 rounded hover:bg-[#2E69A4] hover:text-white transition-colors">
                          {action}
                        </button>
                      ))}
                    </div>
                  </div>
                </div>
              </div>
            ))}
          </div>
        </div>
      </div>
    </div>
  );
};

export default Dashboard;